@import "breakpoints";

.css-box-shadow-container {
  padding-top: 100px;
  display: flex;
  justify-content: center;
  .container {
    width: 80%;

    .header {
      text-align: center;
      h1 {
        font-size: 5em;
        font-family: "Lobster", cursive;
      }
      p {
        color: #606c38;
        font-size: 1.3em;
        width: 80%;
        margin-left: 10%;
      }
    }
    .explore-boxes-container {
      display: flex;
      justify-content: center;
      // width: 100%;
      .explore-boxes {
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
        align-content: center;
        margin-top: 100px;
        width: $width-xl;
        .box-container {
          display: flex;
          justify-content: center;
          align-items: center;
          width: 200px;
          height: 200px;
          border-radius: 5px;
          cursor: pointer;
          margin-bottom: 100px;
          margin-right: 100px;
        }
      }
    }
  }
}
